<template>
  <div class="file-list">
    <!-- 操作栏 -->
    <el-card class="action-card" shadow="never">
      <div class="action-bar">
        <div class="action-left">
          <el-upload
            :action="uploadAction"
            :headers="uploadHeaders"
            :on-success="handleUploadSuccess"
            :on-error="handleUploadError"
            :before-upload="beforeUpload"
            :show-file-list="false"
            multiple
          >
            <el-button type="primary">
              <el-icon><Upload /></el-icon>
              上传文件
            </el-button>
          </el-upload>
          
          <el-button
            type="success"
            @click="handleBatchOCR"
            :disabled="selectedFiles.length === 0"
          >
            <el-icon><Camera /></el-icon>
            批量OCR
          </el-button>
          
          <el-button
            type="danger"
            @click="handleBatchDelete"
            :disabled="selectedFiles.length === 0"
          >
            <el-icon><Delete /></el-icon>
            批量删除
          </el-button>
        </div>
        
        <div class="action-right">
          <el-input
            v-model="searchKeyword"
            placeholder="搜索文件名"
            clearable
            @keyup.enter="handleSearch"
            style="width: 200px"
          >
            <template #append>
              <el-button @click="handleSearch">
                <el-icon><Search /></el-icon>
              </el-button>
            </template>
          </el-input>
          
          <el-select
            v-model="fileTypeFilter"
            placeholder="文件类型"
            clearable
            @change="handleSearch"
            style="width: 120px"
          >
            <el-option label="图片" value="image" />
            <el-option label="文档" value="document" />
            <el-option label="视频" value="video" />
            <el-option label="音频" value="audio" />
            <el-option label="其他" value="other" />
          </el-select>
          
          <el-tooltip content="刷新">
            <el-button circle @click="loadFiles">
              <el-icon><Refresh /></el-icon>
            </el-button>
          </el-tooltip>
          
          <el-tooltip content="列表视图">
            <el-button
              circle
              :type="viewMode === 'list' ? 'primary' : 'default'"
              @click="viewMode = 'list'"
            >
              <el-icon><List /></el-icon>
            </el-button>
          </el-tooltip>
          
          <el-tooltip content="网格视图">
            <el-button
              circle
              :type="viewMode === 'grid' ? 'primary' : 'default'"
              @click="viewMode = 'grid'"
            >
              <el-icon><Grid /></el-icon>
            </el-button>
          </el-tooltip>
        </div>
      </div>
    </el-card>

    <!-- 文件列表 -->
    <el-card class="list-card" v-loading="loading">
      <!-- 列表视图 -->
      <el-table
        v-if="viewMode === 'list'"
        :data="files"
        @selection-change="handleSelectionChange"
        stripe
        style="width: 100%"
      >
        <el-table-column type="selection" width="55" />
        
        <el-table-column label="文件" min-width="300">
          <template #default="{ row }">
            <div class="file-info">
              <div class="file-icon">
                <el-image
                  v-if="isImage(row.mimeType)"
                  :src="getFileUrl(row)"
                  :preview-src-list="[getFileUrl(row)]"
                  fit="cover"
                  style="width: 40px; height: 40px; border-radius: 4px;"
                />
                <el-icon v-else :size="32" :color="getFileTypeColor(row.mimeType)">
                  <component :is="getFileTypeIcon(row.mimeType)" />
                </el-icon>
              </div>
              <div class="file-details">
                <div class="file-name">{{ row.originalName }}</div>
                <div class="file-meta">{{ formatFileSize(row.size) }} • {{ row.mimeType }}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        
        <el-table-column prop="uploader" label="上传者" width="120">
          <template #default="{ row }">
            {{ row.uploader?.username || '未知' }}
          </template>
        </el-table-column>
        
        <el-table-column prop="createdAt" label="上传时间" width="180">
          <template #default="{ row }">
            {{ formatTime(row.createdAt) }}
          </template>
        </el-table-column>
        
        <el-table-column label="操作" width="200" fixed="right">
          <template #default="{ row }">
            <el-button size="small" @click="handleDownload(row)">
              下载
            </el-button>
            
            <el-button
              size="small"
              type="success"
              @click="handleOCR(row)"
              v-if="isImage(row.mimeType)"
            >
              OCR
            </el-button>
            
            <el-popconfirm
              title="确定要删除这个文件吗？"
              @confirm="handleDelete(row)"
              v-if="canDelete(row)"
            >
              <template #reference>
                <el-button size="small" type="danger">
                  删除
                </el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>

      <!-- 网格视图 -->
      <div v-else class="grid-view">
        <div
          v-for="file in files"
          :key="file.id"
          class="file-card"
          @click="handleFileClick(file)"
        >
          <div class="file-preview">
            <el-image
              v-if="isImage(file.mimeType)"
              :src="getFileUrl(file)"
              :preview-src-list="[getFileUrl(file)]"
              fit="cover"
              class="preview-image"
            />
            <div v-else class="file-type-icon">
              <el-icon :size="48" :color="getFileTypeColor(file.mimeType)">
                <component :is="getFileTypeIcon(file.mimeType)" />
              </el-icon>
            </div>
          </div>
          
          <div class="file-card-info">
            <div class="file-card-name" :title="file.originalName">
              {{ file.originalName }}
            </div>
            <div class="file-card-meta">
              {{ formatFileSize(file.size) }}
            </div>
            <div class="file-card-time">
              {{ formatTime(file.createdAt) }}
            </div>
          </div>
          
          <div class="file-card-actions" @click.stop>
            <el-button size="small" @click="handleDownload(file)">
              <el-icon><Download /></el-icon>
            </el-button>
            
            <el-button
              size="small"
              type="success"
              @click="handleOCR(file)"
              v-if="isImage(file.mimeType)"
            >
              <el-icon><Camera /></el-icon>
            </el-button>
            
            <el-popconfirm
              title="确定要删除吗？"
              @confirm="handleDelete(file)"
              v-if="canDelete(file)"
            >
              <template #reference>
                <el-button size="small" type="danger">
                  <el-icon><Delete /></el-icon>
                </el-button>
              </template>
            </el-popconfirm>
          </div>
        </div>
      </div>

      <!-- 分页 -->
      <div class="pagination-wrapper">
        <el-pagination
          v-model:current-page="pagination.page"
          v-model:page-size="pagination.pageSize"
          :page-sizes="[12, 24, 48, 96]"
          :total="pagination.total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handlePageChange"
        />
      </div>
    </el-card>

    <!-- OCR结果对话框 -->
    <el-dialog
      v-model="ocrDialogVisible"
      title="OCR识别结果"
      width="600px"
    >
      <div v-if="ocrResult" class="ocr-result">
        <div class="ocr-image">
          <el-image
            :src="getFileUrl(currentOCRFile)"
            fit="contain"
            style="max-height: 200px;"
          />
        </div>
        
        <div class="ocr-text">
          <h4>识别文本：</h4>
          <el-input
            v-model="ocrResult.text"
            type="textarea"
            :rows="8"
            readonly
          />
          
          <div class="ocr-meta">
            <p>置信度：{{ (ocrResult.confidence * 100).toFixed(1) }}%</p>
            <p>语言：{{ ocrResult.language }}</p>
          </div>
        </div>
      </div>
      
      <div v-else class="ocr-loading">
        <el-icon class="is-loading"><Loading /></el-icon>
        <p>正在识别中，请稍候...</p>
      </div>
      
      <template #footer>
        <el-button @click="ocrDialogVisible = false">关闭</el-button>
        <el-button
          type="primary"
          @click="copyOCRResult"
          v-if="ocrResult"
        >
          复制文本
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useAuthStore } from '@/stores/auth'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { FileInfo, PaginatedResponse, OCRResponse } from '@/types'
import dayjs from 'dayjs'

const authStore = useAuthStore()

// 响应式数据
const loading = ref(false)
const viewMode = ref<'list' | 'grid'>('grid')
const files = ref<FileInfo[]>([])
const selectedFiles = ref<FileInfo[]>([])
const searchKeyword = ref('')
const fileTypeFilter = ref('')
const ocrDialogVisible = ref(false)
const ocrResult = ref<OCRResponse | null>(null)
const currentOCRFile = ref<FileInfo | null>(null)

// 分页
const pagination = reactive({
  page: 1,
  pageSize: 24,
  total: 0
})

// 计算属性
const uploadAction = computed(() => API_ENDPOINTS.FILES.UPLOAD)
const uploadHeaders = computed(() => ({
  Authorization: `Bearer ${authStore.token}`
}))

// 方法
const formatTime = (time: string) => {
  return dayjs(time).format('YYYY-MM-DD HH:mm')
}

const formatFileSize = (size: number) => {
  if (size < 1024) return `${size} B`
  if (size < 1024 * 1024) return `${(size / 1024).toFixed(1)} KB`
  if (size < 1024 * 1024 * 1024) return `${(size / (1024 * 1024)).toFixed(1)} MB`
  return `${(size / (1024 * 1024 * 1024)).toFixed(1)} GB`
}

const isImage = (mimeType: string) => {
  return mimeType.startsWith('image/')
}

const getFileUrl = (file: FileInfo) => {
  return `${API_ENDPOINTS.FILES.DOWNLOAD(file.id)}`
}

const getFileTypeIcon = (mimeType: string) => {
  if (mimeType.startsWith('image/')) return 'Picture'
  if (mimeType.startsWith('video/')) return 'VideoPlay'
  if (mimeType.startsWith('audio/')) return 'Headphone'
  if (mimeType.includes('pdf')) return 'Document'
  if (mimeType.includes('word') || mimeType.includes('document')) return 'Document'
  if (mimeType.includes('excel') || mimeType.includes('spreadsheet')) return 'Document'
  if (mimeType.includes('powerpoint') || mimeType.includes('presentation')) return 'Document'
  if (mimeType.includes('zip') || mimeType.includes('rar')) return 'FolderOpened'
  return 'Document'
}

const getFileTypeColor = (mimeType: string) => {
  if (mimeType.startsWith('image/')) return '#67c23a'
  if (mimeType.startsWith('video/')) return '#409eff'
  if (mimeType.startsWith('audio/')) return '#e6a23c'
  if (mimeType.includes('pdf')) return '#f56c6c'
  if (mimeType.includes('word')) return '#409eff'
  if (mimeType.includes('excel')) return '#67c23a'
  if (mimeType.includes('powerpoint')) return '#e6a23c'
  return '#909399'
}

const canDelete = (file: FileInfo) => {
  return authStore.hasPermission('files', 'delete') && 
         (authStore.hasPermission('files', 'delete', 'all') || file.uploadedBy === authStore.user?.id)
}

// 加载文件列表
const loadFiles = async () => {
  try {
    loading.value = true
    
    const params: any = {
      page: pagination.page,
      pageSize: pagination.pageSize
    }
    
    if (searchKeyword.value) {
      params.keyword = searchKeyword.value
    }
    
    if (fileTypeFilter.value) {
      params.type = fileTypeFilter.value
    }
    
    const response = await http.get<PaginatedResponse<FileInfo>>(
      API_ENDPOINTS.FILES.LIST,
      { params }
    )
    
    files.value = response.items
    pagination.total = response.total
    
  } catch (error) {
    console.error('Failed to load files:', error)
    ElMessage.error('加载文件列表失败')
  } finally {
    loading.value = false
  }
}

// 搜索处理
const handleSearch = () => {
  pagination.page = 1
  loadFiles()
}

// 分页处理
const handlePageChange = (page: number) => {
  pagination.page = page
  loadFiles()
}

const handleSizeChange = (size: number) => {
  pagination.pageSize = size
  pagination.page = 1
  loadFiles()
}

// 选择处理
const handleSelectionChange = (selection: FileInfo[]) => {
  selectedFiles.value = selection
}

// 文件上传处理
const beforeUpload = (file: File) => {
  const isLt50M = file.size / 1024 / 1024 < 50
  
  if (!isLt50M) {
    ElMessage.error('文件大小不能超过 50MB')
    return false
  }
  
  return true
}

const handleUploadSuccess = (response: any) => {
  ElMessage.success('文件上传成功')
  loadFiles()
}

const handleUploadError = (error: any) => {
  console.error('File upload failed:', error)
  ElMessage.error('文件上传失败')
}

// 文件操作
const handleFileClick = (file: FileInfo) => {
  if (isImage(file.mimeType)) {
    // 图片文件直接预览
    return
  }
  
  // 其他文件下载
  handleDownload(file)
}

const handleDownload = async (file: FileInfo) => {
  try {
    const response = await http.get(
      API_ENDPOINTS.FILES.DOWNLOAD(file.id),
      { responseType: 'blob' }
    )
    
    // 创建下载链接
    const blob = new Blob([response])
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.href = url
    link.download = file.originalName
    link.click()
    window.URL.revokeObjectURL(url)
    
  } catch (error) {
    console.error('Download failed:', error)
    ElMessage.error('下载失败')
  }
}

const handleOCR = async (file: FileInfo) => {
  if (!isImage(file.mimeType)) {
    ElMessage.warning('只能对图片文件进行OCR识别')
    return
  }
  
  try {
    currentOCRFile.value = file
    ocrResult.value = null
    ocrDialogVisible.value = true
    
    const response = await http.post<OCRResponse>(
      API_ENDPOINTS.FILES.OCR,
      { fileId: file.id }
    )
    
    ocrResult.value = response
    
  } catch (error) {
    console.error('OCR failed:', error)
    ElMessage.error('OCR识别失败')
    ocrDialogVisible.value = false
  }
}

const handleDelete = async (file: FileInfo) => {
  try {
    await http.delete(API_ENDPOINTS.FILES.DELETE(file.id))
    ElMessage.success('文件删除成功')
    loadFiles()
  } catch (error) {
    console.error('Delete failed:', error)
    ElMessage.error('删除失败')
  }
}

// 批量操作
const handleBatchOCR = async () => {
  const imageFiles = selectedFiles.value.filter(file => isImage(file.mimeType))
  
  if (imageFiles.length === 0) {
    ElMessage.warning('请选择图片文件进行OCR识别')
    return
  }
  
  try {
    const promises = imageFiles.map(file => 
      http.post<OCRResponse>(API_ENDPOINTS.FILES.OCR, { fileId: file.id })
    )
    
    const results = await Promise.all(promises)
    
    // 显示批量OCR结果
    const allText = results.map(result => result.text).join('\n\n')
    
    await ElMessageBox.alert(allText, '批量OCR识别结果', {
      confirmButtonText: '复制全部',
      callback: () => {
        navigator.clipboard.writeText(allText)
        ElMessage.success('已复制到剪贴板')
      }
    })
    
  } catch (error) {
    console.error('Batch OCR failed:', error)
    ElMessage.error('批量OCR识别失败')
  }
}

const handleBatchDelete = async () => {
  try {
    await ElMessageBox.confirm(
      `确定要删除选中的 ${selectedFiles.value.length} 个文件吗？`,
      '批量删除',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    )
    
    const promises = selectedFiles.value.map(file => 
      http.delete(API_ENDPOINTS.FILES.DELETE(file.id))
    )
    
    await Promise.all(promises)
    
    ElMessage.success('批量删除成功')
    loadFiles()
    
  } catch (error) {
    if (error !== 'cancel') {
      console.error('Batch delete failed:', error)
      ElMessage.error('批量删除失败')
    }
  }
}

// OCR结果处理
const copyOCRResult = () => {
  if (!ocrResult.value) return
  
  navigator.clipboard.writeText(ocrResult.value.text)
  ElMessage.success('已复制到剪贴板')
}

// 生命周期
onMounted(() => {
  loadFiles()
})
</script>

<style scoped>
.file-list {
  padding: 0;
}

.action-card {
  margin-bottom: 20px;
}

.action-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.action-left {
  display: flex;
  gap: 8px;
}

.action-right {
  display: flex;
  gap: 8px;
  align-items: center;
}

.list-card {
  margin-bottom: 20px;
}

.file-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.file-icon {
  flex-shrink: 0;
}

.file-details {
  flex: 1;
  min-width: 0;
}

.file-name {
  font-weight: 500;
  color: #303133;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.file-meta {
  font-size: 12px;
  color: #909399;
  margin-top: 2px;
}

.grid-view {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  min-height: 400px;
}

.file-card {
  border: 1px solid #ebeef5;
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
}

.file-card:hover {
  border-color: #409eff;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.file-preview {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.preview-image {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.file-type-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.file-card-info {
  text-align: center;
}

.file-card-name {
  font-weight: 500;
  color: #303133;
  margin-bottom: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.file-card-meta {
  font-size: 12px;
  color: #909399;
  margin-bottom: 4px;
}

.file-card-time {
  font-size: 12px;
  color: #c0c4cc;
}

.file-card-actions {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  gap: 4px;
  opacity: 0;
  transition: opacity 0.3s;
}

.file-card:hover .file-card-actions {
  opacity: 1;
}

.pagination-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.ocr-result {
  padding: 20px 0;
}

.ocr-image {
  text-align: center;
  margin-bottom: 20px;
}

.ocr-text h4 {
  margin: 0 0 12px 0;
  font-size: 14px;
  color: #303133;
}

.ocr-meta {
  margin-top: 12px;
}

.ocr-meta p {
  margin: 4px 0;
  font-size: 12px;
  color: #909399;
}

.ocr-loading {
  text-align: center;
  padding: 40px 0;
}

.ocr-loading p {
  margin-top: 16px;
  color: #909399;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .action-bar {
    flex-direction: column;
    gap: 16px;
    align-items: stretch;
  }
  
  .action-left,
  .action-right {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .grid-view {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 12px;
  }
  
  .file-card {
    padding: 12px;
  }
  
  .file-preview {
    height: 80px;
  }
}
</style>